<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> Всего товаров: {{ countProducts }} </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color-id.sync="filterColorId"
      />
      <div
        class="products__loading-info"
        v-if="products.length === 0 && !productsLoadingFailed"
      >
        <h2
          :class="[
            'products__loading-text',
            { 'products__loading-active': productsLoading },
          ]"
          v-if="productsLoading"
        >
          Выполняется загрузка товаров...
        </h2>
        <h2
          :class="[
            'products__loading-text',
            { 'products__loading-failed': !productsLoading },
          ]"
          v-else
        >
          Товаров с выбранными параметрами не найдено
        </h2>
      </div>
      <div class="products__loading-info" v-else-if="productsLoadingFailed">
        <h2
          :class="[
            'products__loading-text',
            { 'products__loading-error': productsLoadingFailed },
          ]"
        >
          Произошла ошибка при загрузке товаров
        </h2>
        <button class="button button--primery" @click.prevent="loadProducts">
          Попробовать ещё раз
        </button>
      </div>
      <section
        :class="[
          'catalog',
          { catalog__loading: productsLoading && products.length > 0 },
        ]"
      >
        <ProductList :products="products"></ProductList>
        <BasePagination
          v-if="products.length > 0"
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import axios from "axios";
import { API_BASE_URL } from "../config";

export default {
  components: { ProductList, BasePagination, ProductFilter },

  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: this.$route.params.filter || 0,
      filterColorId: 0,
      //filterCategoryColor: 0,
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => {
            return {
              ...product,
              image: product.image.file.url,
            };
          })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId,
            },
          })
          .then((response) => (this.productsData = response.data))
          .catch(() => (this.productsLoadingFailed = true))
          .then(() => (this.productsLoading = false));
      }, 2000);
    },
  },

  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>

<style scoped>
.products__loading-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.products__loading-text {
  position: relative;
  padding-left: 50px;
  margin-bottom: 50px;
}
.products__loading-text::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: translateY(-50%);
}
.products__loading-active::before {
  background-image: url("../assets/832.svg");
}
.products__loading-failed::before {
  background-image: url("../assets/1492.gif");
}
.products__loading-error::before {
  background-image: url("../assets/386.gif");
}
.button--primery:hover {
  color: #333;
}
</style>
